import { Injectable, signal } from '@angular/core'
import { CyclistsService, RetrieveCyclist200Response } from 'src/api-cyclist'
import { APIAccountService } from 'src/api-cyclist/api/aPIAccount.service'
import { environment } from 'src/environments/environment'
import { SubscriptionProcessService } from '../utils/subscription-process.service'
import { TranslateService } from '@ngx-translate/core'
import { ToastService, ToastType } from '../utils/toast.service'
import { RegistrationsService } from 'src/api-cyclist/api/registrations.service'
import { ErrorHandlerService } from '../utils/error-handler.service'

export type cyclistState =
  | {
      state: 'unknown'
      blocked_reasons: string[]
    }
  | {
      state: 'active'
      cyclist: RetrieveCyclist200Response
      blocked_reasons: string[]
    }
  | {
      state: 'unconfirmed'
      cyclist: RetrieveCyclist200Response
      blocked_reasons: string[]
      deletionRequested?: boolean
    }
  | {
      state: 'deletion_requested'
      cyclist: RetrieveCyclist200Response
      blocked_reasons: string[]
    }
  | {
      state: 'deleted'
      cyclist: RetrieveCyclist200Response
      blocked_reasons: string[]
    }
  | {
      state: 'blocked'
      cyclist: RetrieveCyclist200Response
      blocked_reasons: string[]
    }
  | {
      state: 'error'
      error: Error
      blocked_reasons: string[]
    }

@Injectable({
  providedIn: 'root',
})
export class AccountService {
  cyclistState = signal<cyclistState>({
    state: 'unknown',
    blocked_reasons: ['Unknown account state'],
  })

  constructor(
    private CyclistsService: CyclistsService,
    private accountService: APIAccountService,
    private subscriptionProcess: SubscriptionProcessService,
    private registrationsService: RegistrationsService,
    private errorHandler: ErrorHandlerService,
    private toastService: ToastService,
    private translateService: TranslateService
  ) {}
  async loadCyclist() {
    await this.registrationsService
      .retrieveRegistration({
        program: environment.program,
        liveMode: environment.production,
      })
      .toPromise()
    return new Promise((resolve) => {
      this.CyclistsService.retrieveCyclist({
        program: environment.program,
        currentProgram: environment.program,
        expand: [
          'registrations.customer,kyc_documents,registrations.grace_period_usage,registrations.memberships',
        ],
        liveMode: environment.production,
      }).subscribe({
        next: (cyclist) => {
          this.checkCyclistAccount(cyclist)
          resolve(cyclist)
        },
        error: async (error) => {
          this.cyclistState.set({
            state: 'error',
            error,
            blocked_reasons: ['Error loading account'],
          })
          this.errorHandler.handleError(error)
          throw error
        },
      })
    })
  }

  async deleteAccount() {
    const currentState = this.cyclistState()
    if (
      currentState.state !== 'unknown' &&
      currentState.state !== 'error' &&
      currentState.state !== 'deletion_requested' &&
      currentState.state !== 'deleted'
    ) {
      this.accountService.deleteMyUser().subscribe({
        next: async () => {
          if ('cyclist' in currentState) {
            if (currentState.state === 'unconfirmed') {
              // Pour les utilisateurs non confirms, on garde l'tat unconfirmed mais on ajoute le flag
              this.cyclistState.set({
                state: 'unconfirmed',
                cyclist: currentState.cyclist,
                blocked_reasons: currentState.blocked_reasons,
                deletionRequested: true,
              })
            } else {
              // Pour les utilisateurs actifs, on passe en deletion_requested
              this.cyclistState.set({
                state: 'deletion_requested',
                cyclist: currentState.cyclist,
                blocked_reasons: ['Deletion requested'],
              })
            }
          }
          this.subscriptionProcess.clearProcess()
        },
        error: async (err) => {
          this.errorHandler.handleError(err)
          throw err
        },
      })
    } else {
      this.toastService.createWithJustMessage(
        this.translateService.instant('generic.deletion_account_impossible'),
        ToastType.Error
      )
      throw new Error('Account is already deleted')
    }
  }

  public changeEmail(email: string) {
    return new Promise((resolve, reject) => {
      this.CyclistsService.changeCyclistEmail({
        changeCyclistEmailRequest: {
          email,
        },
      }).subscribe({
        next: (cyclist) => {
          this.checkCyclistAccount(cyclist)
          this.toastService.createWithJustMessage(
            this.translateService.instant('settings.infos.email_changed'),
            ToastType.Success
          )
          resolve(cyclist)
        },
        error: (error) => {
          this.errorHandler.handleError(error)
          reject(error)
        },
      })
    })
  }

  /**
   * Gets the current cyclist if available, or throws an error if not
   * @returns The current cyclist
   * @throws Error if cyclist is not available
   */
  public getCurrentCyclist(): RetrieveCyclist200Response {
    const currentState = this.cyclistState()
    if (!('cyclist' in currentState)) {
      throw new Error('No cyclist available')
    }
    return currentState.cyclist
  }

  /**
   * Updates the cyclist data while preserving the current state
   * @param cyclist The updated cyclist data
   */
  public updateCyclistData(cyclist: RetrieveCyclist200Response) {
    const currentState = this.cyclistState()
    if (currentState.state !== 'unknown' && currentState.state !== 'error') {
      this.cyclistState.set({
        ...currentState,
        cyclist,
      })
    }
  }

  public checkCyclistAccount(cyclist: RetrieveCyclist200Response) {
    const currentState = this.cyclistState()

    // Helper function to filter out "Unknown account state" from blocked reasons
    const getFilteredBlockedReasons = (reasons: string[]) =>
      reasons.filter((reason) => reason !== 'Unknown account state')

    if ('blocked_reasons' in currentState) {
      // Check for external users FIRST - role takes priority over confirmation status

      // For regular cyclists, check confirmation status
      switch (cyclist.status) {
        case 'active':
          this.cyclistState.set({
            state: 'active',
            cyclist,
            blocked_reasons: getFilteredBlockedReasons(
              currentState.blocked_reasons
            ),
          })
          break
        case 'unconfirmed':
          this.cyclistState.set({
            state: 'unconfirmed',
            cyclist,
            blocked_reasons: [
              ...getFilteredBlockedReasons(currentState.blocked_reasons),
              'Account not confirmed',
            ],
            deletionRequested: false,
          })
          break
        case 'deleted':
          this.cyclistState.set({
            state: 'deleted',
            cyclist,
            blocked_reasons: [
              ...getFilteredBlockedReasons(currentState.blocked_reasons),
              'Account deleted',
            ],
          })
          break
        case 'deletion_requested':
          this.cyclistState.set({
            state: 'deletion_requested',
            cyclist,
            blocked_reasons: [
              ...getFilteredBlockedReasons(currentState.blocked_reasons),
              'Account deletion requested',
            ],
          })
          break
        case 'blocked':
          this.cyclistState.set({
            state: 'blocked',
            cyclist,
            blocked_reasons: [
              ...getFilteredBlockedReasons(currentState.blocked_reasons),
              'Account blocked',
            ],
          })
          break
        default:
          this.cyclistState.set({
            state: 'unknown',
            blocked_reasons: [
              ...getFilteredBlockedReasons(currentState.blocked_reasons),
              'Unknown account state',
            ],
          })
          break
      }

      // Check for registration-level blocking (only for non-external users)
      if (cyclist.registrations?.data![0].status === 'blocked') {
        this.cyclistState.set({
          state: 'blocked',
          cyclist,
          blocked_reasons: [
            ...getFilteredBlockedReasons(currentState.blocked_reasons),
            'Account blocked',
          ],
        })
      }
    }
  }

  /**
   * Safely gets blocked reasons from the current cyclist state
   * External users bypass blocked reasons
   * @returns Array of blocked reasons, empty array if none exist or if user is external
   */
  public getBlockedReasons(): string[] {
    const currentState = this.cyclistState()
    return 'blocked_reasons' in currentState ? currentState.blocked_reasons : []
  }

  /**
   * Checks if the cyclist has any blocked reasons
   * @returns true if there are blocked reasons, false otherwise
   */
  public hasBlockedReasons(): boolean {
    return this.getBlockedReasons().length > 0
  }

  /**
   * Checks if the cyclist has a specific blocked reason
   * @param reason The reason to check for
   * @returns true if the reason exists, false otherwise
   */
  public hasBlockedReason(reason: string): boolean {
    return this.getBlockedReasons().includes(reason)
  }

  public addBlockedReason(reason: string) {
    const currentState = this.cyclistState()
    if (
      'cyclist' in currentState &&
      (currentState.cyclist.role == 'guest' ||
        currentState.cyclist.registrations?.data?.[0]?.role == 'guest')
    ) {
      return
    }
    if (
      'blocked_reasons' in currentState &&
      !currentState.blocked_reasons.includes(reason)
    ) {
      this.cyclistState.set({
        ...currentState,
        blocked_reasons: [...currentState.blocked_reasons, reason],
      })
    }
  }

  public removeBlockedReason(reason: string) {
    const currentState = this.cyclistState()
    if (
      currentState.state !== 'unknown' &&
      currentState.state !== 'error' &&
      'blocked_reasons' in currentState &&
      currentState.blocked_reasons.includes(reason)
    ) {
      const newReasons = currentState.blocked_reasons.filter(
        (r) => r !== reason
      )
      this.cyclistState.set({
        ...currentState,
        blocked_reasons: newReasons,
      })
    }
  }

  public hasMissingPaymentMethodOrCgu(): boolean {
    const cyclistState = this.cyclistState()
    if (
      'cyclist' in cyclistState &&
      (cyclistState.cyclist.role == 'guest' ||
        cyclistState.cyclist.registrations?.data?.[0]?.role == 'guest')
    ) {
      return false
    }
    if (cyclistState.state !== 'unknown' && cyclistState.state !== 'error') {
      if (
        !cyclistState.cyclist.registrations?.data![0].terms_validated ||
        !cyclistState.cyclist.registrations?.data![0].customer?.invoice_settings
          .default_payment_method
      ) {
        return true
      } else {
        return false
      }
    } else {
      return true
    }
  }

  /**
   * Checks if the current user has requested account deletion
   * @returns true if deletion has been requested, false otherwise
   */
  public hasRequestedDeletion(): boolean {
    const currentState = this.cyclistState()
    if (currentState.state === 'deletion_requested') {
      return true
    }
    if (
      currentState.state === 'unconfirmed' &&
      currentState.deletionRequested
    ) {
      return true
    }
    return false
  }
}
