import { Injectable } from '@angular/core'
import { StorageService } from './storage.service'
import { BehaviorSubject, Observable } from 'rxjs'
import {
  ListForfait200ResponseAllOfDataInner,
  CreateAbonnementRequestSupportingDocumentsInner,
} from 'src/api-cyclist'

interface SubscriptionProcess {
  forfait: ListForfait200ResponseAllOfDataInner | null
  startedAt: number
  reduction?: string | null
  supportingDocuments?: CreateAbonnementRequestSupportingDocumentsInner[]
  currentStep: 'forfait' | 'payment' | 'documents' | 'phone' | 'completed'
}

@Injectable({
  providedIn: 'root',
})
export class SubscriptionProcessService {
  // Constantes de configuration
  private static readonly STORAGE_KEY = 'subscription_process'

  // tat du processus d'abonnement actuel
  private currentProcess = new BehaviorSubject<SubscriptionProcess | null>(null)

  constructor(private storage: StorageService) {
    this.initFromStorage()
  }

  // Mthodes d'initialisation et de gestion du stockage
  private async initFromStorage() {
    const stored = await this.storage.get(
      SubscriptionProcessService.STORAGE_KEY
    )
    if (stored) {
      this.currentProcess.next(stored)
    }
  }

  // Mthodes de gestion du processus d'abonnement
  async startProcess(
    forfait: ListForfait200ResponseAllOfDataInner | null,
    reduction?: string
  ) {
    const process: SubscriptionProcess = {
      forfait,
      startedAt: Date.now(),
      reduction: reduction && reduction.length > 0 ? reduction : null,
      currentStep: 'forfait',
    }
    await this.storage.set(SubscriptionProcessService.STORAGE_KEY, process)
    this.currentProcess.next(process)
  }

  // Mthodes de mise  jour
  async updateStep(step: SubscriptionProcess['currentStep']) {
    const current = this.currentProcess.value
    if (current) {
      const updated = { ...current, currentStep: step }
      await this.storage.set(SubscriptionProcessService.STORAGE_KEY, updated)
      this.currentProcess.next(updated)
    }
  }

  async updateSupportingDocuments(
    documents: CreateAbonnementRequestSupportingDocumentsInner[]
  ) {
    const current = this.currentProcess.value
    if (current) {
      const updated = { ...current, supportingDocuments: documents }
      await this.storage.set(SubscriptionProcessService.STORAGE_KEY, updated)
      this.currentProcess.next(updated)
    }
  }

  async updateReduction(reduction: string) {
    const current = this.currentProcess.value
    if (current) {
      // Si un processus existe dj, on met  jour la rduction
      if (reduction.length > 0) {
        const updated = { ...current, reduction }
        await this.storage.set(SubscriptionProcessService.STORAGE_KEY, updated)
        this.currentProcess.next(updated)
      } else {
        const updated = { ...current, reduction: null }
        await this.storage.set(SubscriptionProcessService.STORAGE_KEY, updated)
        this.currentProcess.next(updated)
      }
    } else {
      this.startProcess(null, reduction)
    }
  }

  // Mthodes utilitaires
  async clearProcess() {
    await this.storage.remove(SubscriptionProcessService.STORAGE_KEY)
    this.currentProcess.next(null)
  }

  getCurrentProcess(): Observable<SubscriptionProcess | null> {
    return this.currentProcess.asObservable()
  }

  // Mthodes de validation et de prparation des donnes
  async hasForfaitSelected(): Promise<boolean> {
    const process = this.currentProcess.value
    return !!process?.forfait?.id
  }

  async getCreateAbonnementPayload() {
    const process = this.currentProcess.value
    if (!process?.forfait?.id) return null

    return {
      forfaitID: process.forfait.id,
      ...(process.reduction && { reduction: process.reduction }),
      ...(process.supportingDocuments && {
        supporting_documents: process.supportingDocuments,
      }),
    }
  }
}
